@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("./fonts/Inter-Regular.woff2?v=3.19") format("woff2"),
    url("./fonts/Inter-Regular.woff?v=3.19") format("woff"),
    url("./fonts/Inter-Regular.ttf?v=3.19") format("truetype"),
    local("Inter Regular"), local("Inter-Regular");
}

